import React, {useEffect, useState} from "react";
import style from "./FindInput.module.css";
import Data from "./../../Data/Data.json";
import {useNavigate} from "react-router";

const FindInput = () => {
  const navigate = useNavigate();

  const [ListCategory, setList] = useState(Object.keys(Data));
  const [listName, setFindRes] = useState([]);
  const [value, setValue] = useState("");
  const [listFindName, setListFindName] = useState();

  const CleaarInput = () => {
    setValue("");
    navigate("/mgwf");
  };

  const Finde = () => {
    if (listName && value) {
      localStorage.setItem("listFind", JSON.stringify(listName));
      navigate("/mgwf/findlist");
    } else {
      return null;
    }
  };
  const OpenSaner = () => {
    navigate("/scaner");
  };
  const FindChange = (e) => {
    const result = [];
    setValue(e.target.value);

    const ListValue = e.target.value.split(" ");
    console.log(ListValue);
    for (const key in Data) {
      Data[key].map((item) => {
        if (
          item.Name.toLowerCase().trim().includes(e.target.value.toLowerCase())
        ) {
          result.push({item: item, category: key});
        }
      });
      setFindRes(result);
    }
  };

  return (
    <div className={style.wrapper}>
      <div>
        <input
          type="text"
          placeholder=""
          value={value}
          onChange={(e) => FindChange(e)}
        ></input>
        <button className={style.btnClear} onClick={() => CleaarInput()}>
          {" "}
          x
        </button>
        <button className={style.scaner} onClick={() => OpenSaner()}></button>
      </div>
      <button className={style.btnFind} onClick={() => Finde()}>
        {" "}
        
      </button>
    </div>
  );
};

export default FindInput;
